import React from "react";
import styled from "styled-components";
import Container from "./container";
import LangSelect from "./lang-select";
import LinkWithLang from "./link-with-lang";
import { useStaticQuery, graphql } from "gatsby";
import { useTranslation } from "react-i18next";

const Header = ({ langKey }) => {
  const { t } = useTranslation();

  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
          }
        }
      }
    `
  );

  return (
    <StyledHeader>
      <HeaderWrapper>
        <HeaderTitle>
          <LinkWithLang langKey={langKey} to="/index">
            {site.siteMetadata.title}
          </LinkWithLang>
        </HeaderTitle>

        <HeaderNavList>
          <HeaderNavListItem>
            <LinkWithLang langKey={langKey} to="/blog">
              {t("blog")}
            </LinkWithLang>
          </HeaderNavListItem>

          <HeaderNavListItem>
            <LinkWithLang langKey={langKey} to="/about">
              {t("about")}
            </LinkWithLang>
          </HeaderNavListItem>

          <HeaderNavListItem>
            <LinkWithLang langKey={langKey} to="/contact">
              {t("contact")}
            </LinkWithLang>
          </HeaderNavListItem>
          <HeaderNavListItem>
            <LangSelect value={langKey} />
          </HeaderNavListItem>
        </HeaderNavList>
      </HeaderWrapper>
    </StyledHeader>
  );
};

export default Header;

const HeaderNavList = ({ children }) => {
  return (
    <StyledNav>
      <StyledNavList>{children}</StyledNavList>
    </StyledNav>
  );
};

const HeaderNavListItem = ({ children, style }) => {
  return <StyledNavListItem style={style}>{children}</StyledNavListItem>;
};

const StyledHeader = styled.header`
  padding-top: var(--size-300);
`;

const HeaderWrapper = styled(Container)`
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const HeaderTitle = styled.div`
  & a {
    text-transform: uppercase;
    text-decoration: none;
    font-size: var(--size-400);
    color: inherit;
  }
`;

const StyledNav = styled.nav`
  position: static;
  padding: 0;
  background: transparent;
  backdrop-filter: unset;
`;

const StyledNavList = styled.ul`
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 0;
  list-style-type: none;
`;

const StyledNavListItem = styled.li`
  &:not(:last-of-type) {
    margin-right: 2rem;
  }
  @media screen and (max-width: 700px) {
    &:not(:last-of-type) {
      margin-right: 1rem;
    }
  }
  & a {
    color: inherit;
    text-transform: uppercase;
    font-size: var(--size-300);
    text-decoration: none;
    letter-spacing: 0.1rem;
  }
  @media screen and (max-width: 700px) {
    & a {
      font-size: 0.7rem;
    }
  }
`;
